.btn {
  position: relative;
  width: 12rem;
  height: 12rem;
  background-color: rgb(0, 0, 0, 1);
  display: inline-block;
  margin: 1rem;
  padding: 0rem rem;
  outline: none;
  border: 0rem solid white;
  font-size: 9rem;
  color: white;
  font-weight: normal;
  border-radius: 1.5rem;
  cursor: pointer;
  left: 3rem;
  top: 1rem;
}
.playgameBtn {
  position: absolute;
  color: black;
  text-align: centre;
  font-size: 3rem;
  font-weight: bold;
  width: 70rem;
  height: 4.5rem;
  background-color: mediumseagreen;
  left: 8rem;
  top: 25rem;
  font-style: oblique;
  padding: 1.9rem;
}
.homepage {
  text-align: center;
  font-size: 6rem;
  color: blanchedalmond;
}
.homeBtn {
  position: absolute;
  color: black;
  text-align: centre;
  font-size: 1.5rem;
  font-weight: bolder;
  width: 7rem;
  height: 3.5rem;
  background-color: darkorange;
  left: 1rem;
  top: 1rem;
  font-style: oblique;
  padding: 1rem;
  border-radius: 2rem;
}
.playBtn {
  color: white;
  position: relative;
  padding: 1rem;
  background-color: black;
  display: inline-block;
  font-size: 1.3rem;
  border-radius: 0.5rem;
  font-weight: bold;
  left: 2rem;
  top: 1rem;
}
body {
  background-color: black;
}
.paraone {
  position: relative;
  background-color: darkorange;
  height: 15rem;
  margin-right: 0rem;
  top: 0rem;
  left: 0rem;
  width: 48rem;
}
.paratwo {
  position: relative;
  background-color: antiquewhite;
  height: 48rem;
  margin-right: 0rem;
  top: -2.1rem;
  left: 0rem;
  width: 48rem;
  border-top: 0.3rem solid black;
  border-bottom: 0.3rem solid black;
}

.musicBtn {
  position: relative;
  padding: 0.9rem;
  background-color: black;
  display: inline-block;
  margin: 0.3rem;
  font-size: 1.2rem;
  color: white;
  border-radius: 0.5rem;
  font-weight: bold;
  left: 25rem;
  top: 1rem;
}

.container {
  position: relative;
  width: 38.5rem;
  height: 60rem;
  border: 0rem solid rgba(0, 0, 0, 1);
  border-radius: 2rem;
  padding: 0rem;
  box-sizing: border-box;
  top: 0rem;
  left: 0rem;
  right: 0rem;
  margin: auto;
}
.heading {
  font-size: 3rem;
  font-weight: bold;
  color: white;
  text-align: center;
}
